import React from "react";

function ProgressBar(props) {
	const outerDivStyles = {
		width: "250px",
		height: "20px",
		background: "#fcfcfc",
		position: "relative",
		boxShadow: "2px 2px 10px inset #333"
	}
	const innerDivStyles = {
		background: "#333",
		position: "absolute",
		left: 0,
		top: 0,
		bottom: 0,
		right: 0,
		width: `${props.progressValue}%`,
	}
	return (
		<div className="progressbar-container">
			<div style={outerDivStyles}>
				<div style={innerDivStyles}>
				</div>
			</div>
			<span>{`${props.progressValue}%`}</span>
		</div>
	)
}

export default ProgressBar;