import React from "react";
import { NavLink } from "react-router-dom";

function HeaderLinks(){
    return(
			<header className="top-header">
					<ul className="header-list">
						<li className="header-list-item"><NavLink exact activeClassName="is-active-link" to="/myfolio">About Me</NavLink></li>
						<li className="header-list-item"><NavLink activeClassName="is-active-link" to="/experience">Experience</NavLink></li>
						<li className="header-list-item"><NavLink activeClassName="is-active-link" to="/skills">Skills</NavLink></li>
						<li className="header-list-item"><NavLink activeClassName="is-active-link" to="/education">Education</NavLink></li>
					</ul>
			</header>
    )
}

export default HeaderLinks;