import React from 'react';

import img from "../images/small-img.jpg";

class AboutMe extends React.Component {
	render() {
		return (
			<section className="profile-section clearfix">
				{/* <h1 className="page-title">About Me</h1> */}
				<div className="image-section"><img src={img} className="profile-image" /></div>
				<div className="width250 float-right text-align-right">
					<h2 className="page-subtitle">Amol G. Nadagonde</h2>
					<p className="one-liner">I am a designer and developer, who likes to create trending websites and apps.</p>
				</div>
			</section>
		)
	}
}

export default AboutMe;
