import React from 'react';

import ProgressBar from "./subcomponents/ProgressBar";

class Skills extends React.Component {
	render() {
		return (
			<section className="skills-section padding-top60">
				<ul className="skills-list">
					<li className="skills-list-item">
						<h5 className="block-title text-transform-uppercase">HTML</h5>
						<ProgressBar progressValue={80} />
					</li>
					<li className="skills-list-item">
						<h5 className="block-title text-transform-uppercase">CSS</h5>
						<ProgressBar progressValue={80} />
					</li>
					<li className="skills-list-item">
						<h5 className="block-title text-transform-uppercase">Javascript</h5>
						<ProgressBar progressValue={70} />
					</li>
					<li className="skills-list-item">
						<h5 className="block-title text-transform-uppercase">React</h5>
						<ProgressBar progressValue={40} />
					</li>
					<li className="skills-list-item">
						<h5 className="block-title text-transform-uppercase">Photoshop</h5>
						<ProgressBar progressValue={60} />
					</li>
				</ul>
			</section>
		)
	}
}

export default Skills;
