import React from 'react';
import content from "../myinfo";

function RightArrow({ nextClick }) {
	return (
		<i className="slider-arrow right-arrow" onClick={nextClick}></i>
	)
}

function LeftArrow({ prevClick }) {
	return (
		<i className="slider-arrow left-arrow" onClick={prevClick}></i>
	)
}

class Experience extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			sliderContent: content.experience,
			currentSliderValue: 0,
		}
	}

	getNextSlide = () => {
		if(this.state.currentSliderValue < this.state.sliderContent.length - 1){
			this.setState(prevState => {
				return ({
					currentSliderValue: prevState.currentSliderValue + 1,
				})
			})
		} else {
			this.setState(prevState => {
				return ({
					currentSliderValue: 0
				})
			})
		}
	}

	getPrevSlide = () => {
		if(this.state.currentSliderValue > 0){
			this.setState(prevState => {
				return ({
					currentSliderValue: prevState.currentSliderValue - 1
				})
			})
		}
	}

	render() {
		const contentToRender = this.state.sliderContent[this.state.currentSliderValue]

		return (
			<section className="experience-section" >
				{/* <h1 className="page-title">My Experience</h1> */}
				<RightArrow nextClick={this.getNextSlide} />
				<LeftArrow prevClick={this.getPrevSlide} />
				<ul className="exp-list">
					<li className="exp-list-item">
						<div className="exp-top-section">
							<div className="exp-top-blocks">
								<h5 className="block-title">Company</h5>
								<p>{contentToRender.company}</p>
							</div>
							<div className="exp-top-blocks">
								<h5 className="block-title">Duration</h5>
								<p>{contentToRender.duration}</p>
							</div>
							<div className="exp-top-blocks">
								<h5 className="block-title">Role</h5>
								<p>{contentToRender.role}</p>
							</div>
						</div>
						<div className="exp-content-container">
							<h5 className="block-title">Description</h5>
							<p>{contentToRender.description}</p>
						</div>
						<div className="exp-content-container">
							<h5 className="block-title">Responsibilities</h5>
							<ul className="description-list">
								{contentToRender.responsibilities.map((item, index) => {
									return (
										<li key={index} className="description-list-item">{item}</li>
									)
								})}
							</ul>
						</div>
					</li>
				</ul>
			</section >
		)
	}
}

export default Experience;
